/* dropdown */
.dropdown-custom.btn {
  font-size: 14px;
  border: 0px;
  position: relative;
  top: -1px;
  overflow: unset !important;
  letter-spacing: normal;
  font-weight: 800;
  padding: 0 20px 0 0;
  background: none !important;
  &::before {
    content: "";
    position: absolute;
    top: -10px;
    left: 19px;
    width: max-content;
    height: auto;
    padding: 30px 40px;
    background: 0;
  }
  &::after {
    margin-left: 5px !important;
  }
  &:focus {
    box-shadow: none !important;
  }
  &:hover {
    color: $color;
  }
}
.dropdown-toggle::after {
  font-family: FontAwesome;
  content: "\f078";
  color: rgba(0, 0, 0, 0.5);
  padding-left: 2px;
  font-size: 8px;
  top: -1px;
  position: absolute;
  top: 6px;
  right: 5px;
  border: none;
}
.item-dropdown {
  width: max-content;
  position: absolute;
  background: $white;
  border-radius: 5px;
  overflow: hidden;
  inset: 50px auto auto 0;
  padding: 0 0;
