  -webkit-animation: smoothDrop 0.2s ease;
  box-shadow: 2px 2px 30px 0px rgba(20, 20, 20, 0.1);
  z-index: 1;
  .dropdown {
    position: relative;
    text-align: center;
    a {
      color: $black_soft !important;
