.navbar {
  padding: 0;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: auto;
  display: flex;
  align-items: center;
  background: rgba(0, 0, 0, 0);
  border-bottom: solid 1px rgba(72, 57, 116, 0.0);
  z-index: 999;
  transition: all 0.6s ease;
  &.white {
    a {
      color: $black;
    }
    .btn {
      color: $black;
      .lines {
        top: 24px;
      }
    }
  }
  .w-100-nav {
    margin: 0;
    width: 100%;
    justify-content: left;
    align-items: center;
    flex-wrap: nowrap;
  }
  a {
    position: relative;
    font-size: 14px;
    font-weight: 800;
    padding: 0 5px 0 0;
    text-decoration: none !important;
    color: $white;
    outline: none;
    transition: all 0.3s ease;
    &:hover {
      color: inherit;
      transition: all 0.3s ease;
    }
    &.active {
      transition: all 0.3s ease;
      &:before {
