/** contact **/
.form-side {
  border-radius: 4px;
}
.text-side {
  padding: 15px 0;
  margin-top: 15px;
  .address {
    padding-top: 15px;
    line-height: 1.6;
    .heading {
      font-size: 1.4rem;
      font-weight: 500;
      letter-spacing: 1px;
    }
    .list {
      position: relative;
      max-width: 320px;
      display: block;
      padding: 5px 0 15px;
      padding-left: 40px;
      i {
        position: absolute;
        top: 5px;
        left: 5px;
        font-size: 1.3rem;
        display: inline-block;
      }
      a {
        color: $white;
        text-decoration: none;
        transition: all 0.3s ease;
        &:hover {
          color: $color;
          transition: all 0.3s ease;
        }
      }
    }
  }
}
.formcontact {
  display: block;
  width: 100%;
  label {
    display: block;
    width: 100%;
    margin-bottom: 5px;
  }
  input {
    width: 100%;
    font-size: 0.9rem;
    width: 100%;
    padding: 8px 10px;
    border: 1px solid #ebebeb;
    margin-bottom: 15px;
  }
  textarea {
    font-size: 0.9rem;
    width: 100%;
    height: 195px;
    padding: 8px 10px;
    border: 1px solid #ebebeb;
    margin-bottom: 15px;
  }
  .msg-error {
    font-size: 0.87rem;
    opacity: 0.8;
    padding: 5px 0;
  }
  button {
    position: relative;
    overflow: hidden;
    font-size: 11pt;
    color: $white;
    background: none;
    border: 2px solid $color;
    border-radius: 0;
    padding: 8px 50px;
    margin-top: 15px;
    -webkit-transition-delay: 0s;
    transition-delay: 0s;
    z-index: 0;
    span {
      color: $white;
      position: relative;
      z-index: 3;
    }
    &::before {
      content: "";
      width: 100%;
      height: 100%;
      top: 0;
      left: 0;
      position: absolute;
      background-image: linear-gradient(to left, $color, $color_more);
      opacity: 0;
      z-index: 1;
      transition: all 0.3s ease;
    }
    &:hover::before {
      opacity: 1;
      transition: all 0.3s ease;
    }
    &:hover {
      .shine {
        -webkit-animation: sheen 1s alternate;
        animation: sheen 1s alternate;
      }
    }
  }
}
#success,
#failed {
  &.show {
    display: block !important;
    margin-bottom: 15px;
  }
  &.hide {
    display: none;
  }
}
#buttonsent.show {
  pointer-events: none;
}
#buttonsent {
  width: max-content;
  padding: 8px 30px;
}
.padding40.box-rounded.mb30 {
  padding: 40px;
  border-radius: 3px;
  background-color: rgb(242, 246, 254);
  &.text-light {
    color: $white;
    background: $color_more;
    margin-bottom: 0;
    h3 {
      color: $white;
    }
    .btn {
      color: $white;
    }
  }
}
address {
  margin-top: 20px;
  margin-bottom: 1rem;
  font-style: normal;
  line-height: inherit;
  &.s1 span {
    display: block;
    padding: 0;
    font-size: 15px;
    line-height: 1.7em;
    margin-bottom: 5px;
    i {
      color: $color;
      margin-top: 5px;
      margin-left: 5px;
      margin-right: 15px;
      font-size: 15px;
    }
    .btn {
      display: inline-block;
      border: 0;
      color: $color;
      padding: 0;
      margin: 0;
      &:hover {
        background: none;
        padding: 0;
      }
      &:before {
        content: unset;
      }
    }
  }
}

/*** news ***/
.post-content {
  position: relative;
  border-radius: 5px;
  overflow: hidden;
  margin-bottom: 20px;
}
.post-image {
  position: relative;
  img {
    width: 100%;
