}
.dark-scheme .pagination li .a {
  border-color: rgba(255, 255, 255, 0.1);
  background: #21273e;
}
.pagination li:last-child .a {
  border-right: solid 1px #dddddd;
}
.dark-scheme .pagination li:last-child .a {
  border-right-color: rgba(255, 255, 255, 0.1);
}
.pagination > .active > .a {
  color: #ffffff;
  background: $color;
}

/*** create file ***/
.d-create-file {
  padding: 50px;
  border-radius: 10px;
  border: dashed 3px #dddddd;
  text-align: center;
  #get_file {
    margin: 0 auto;
  }
  .browse {
    position: relative;
    width: max-content;
    height: auto;
    margin: 0 auto;
    cursor: pointer;
    #upload_file {
      position: absolute;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
    }
    &:hover {
      .btn-main {
        box-shadow: 2px 2px 20px 0px rgba(131, 100, 226, 0.5);
        transition: all 0.3s ease;
      }
    }
