}

/*** activity ***/
.filter__l {
  font-weight: bold;
}
.filter__r {
  float: right;
  cursor: pointer;
}
.activity-filter {
  margin: 0;
  padding: 0;
  list-style: none;
  li {
    display: inline-block;
    padding: 8px 10px;
    border: solid 1px #dddddd;
    border-radius: 5px;
    margin-right: 5px;
    margin-bottom: 10px;
    width: 48%;
    font-weight: bold;
    cursor: pointer;
    i {
      height: 32px;
      width: 32px;
      padding: 9px 0;
      text-align: center;
      border-radius: 100%;
      background: #eee;
      color: $color;
      margin-right: 10px;
    }
  }
}
.activity-list {
  list-style: none;
  padding: 0;
  margin: 0;
  li {
    padding: 20px;
    padding-right: 20px;
    border-radius: 10px;
    margin-bottom: 20px;
    position: relative;
    padding-right: 100px;
    background: rgba(131, 100, 226, 0.07);
    img {
      width: 80px;
      position: absolute;
      border-radius: 100%;
    }
    &.act_sale {
      img {
        border-radius: 5px;
      }
      &::after {
        content: "\f291";
      }
    }
    &.act_like {
      img {
        border-radius: 5px;
      }
      &::after {
        content: "\f004";
      }
    }
    &.act_offer {
      img {
        border-radius: 5px;
      }
      &::after {
        content: "\f0e3";
      }
    }
    .act_list_text {
      padding-left: 100px;
